import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import './index.css';
import { PageHeader, Form, Input, Button, Select } from 'antd';

import moment from "moment";
import 'moment/locale/th'
import axios from "axios";
import { MAIN_URL } from './server';


const { Option } = Select;
const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 16,
  },
};
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16,
  },
};

const App = () => {

  const [form] = Form.useForm();

  const postApplyLoanData = async (data) => {
    console.log(data)
    await axios.post(MAIN_URL + '/customer/applyloans', data)
      .then((response) => {
        console.log(response.status);
        // getAllReports();
        //   setDataSource(response.data);
      });
  }

  const onFinish = (values) => {
    // console.log(values);
    const data = {
      date: moment(Date.now()).format('yyyy-MM-DD'),
      loan_type: values.loan_type,
      name: values.username,
      address: values.address,
      phone: values.phone,
      detail: values.detail
    }
    postApplyLoanData(data);
  };

  const onReset = () => {
    form.resetFields();
  };

  return (
    <div style={{ padding: 50 }}>

      <PageHeader
        // className="site-page-header"
        // onBack={() => null}
        title=""
      // subTitle=""
      />

      <Form {...layout} form={form} name="control-hooks" onFinish={onFinish}>

        <Form.Item
          name="loan_type"
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select
            placeholder=""
            // onChange={(text) => {
            //   setValue(text {})
            // }}
            allowClear
          >
            <Option value="/">/</Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value="/">/</Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>

          </Select>
        </Form.Item>

        <Form.Item
          name="username"
          label="-"
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input placeholder="- ()" />
        </Form.Item>

        <Form.Item
          name="address"
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input placeholder="..." />
        </Form.Item>

        <Form.Item
          name="phone"
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input placeholder="..." />
        </Form.Item>

        <Form.Item
          name="detail"
          label=""
        >
          <Input.TextArea placeholder=" ()..." />
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            
          </Button>
          <Button htmlType="button" onClick={onReset} style={{ marginLeft: 16 }}>
            
          </Button>
        </Form.Item>
      </Form>
    </div>

  );
}

export default App
